<template>
  <div>
    <div id="myChart" :style="{ height :'400px'}"></div>
  </div>
</template>

<script>
// import "../../assets/js/china.js";
export default {
  data() {
    return {
      mydata: [
        { name: "", value: "100" },
        { name: "", value: this.randomData() },
        { name: "", value: this.randomData() },
        { name: "", value: this.randomData() },
        { name: "", value: this.randomData() },
        { name: "", value: this.randomData() },
        { name: "", value: this.randomData() },
        { name: "", value: this.randomData() },
        { name: "", value: this.randomData() },
        { name: "", value: this.randomData() },
        { name: "", value: this.randomData() },
        { name: "", value: this.randomData() },
        { name: "", value: this.randomData() },
        { name: "", value: this.randomData() },
        { name: "", value: this.randomData() },
        { name: "", value: this.randomData() },
        { name: "", value: this.randomData() },
        { name: "", value: this.randomData() },
        { name: "", value: this.randomData() },
        { name: "", value: this.randomData() },
        { name: "", value: this.randomData() },
        { name: "", value: this.randomData() },
        { name: "", value: this.randomData() },
        { name: "", value: this.randomData() },
        { name: "", value: this.randomData() },
        { name: "", value: this.randomData() },
        { name: "", value: this.randomData() },
        { name: "", value: this.randomData() },
        { name: "", value: this.randomData() },
        { name: "", value: this.randomData() },
        { name: "", value: this.randomData() },
        { name: "", value: this.randomData() },
        { name: "", value: this.randomData() },
        { name: "", value: this.randomData() }
      ]
    };
  },
  methods: {
    randomData() {
      return Math.round(Math.random() * 500);
    }
  },
  mounted() {
    var optionMap = {
      backgroundColor: "#FFFFFF",
      title: {
        text: "",
        subtext: "",
        x: "center"
      },
      tooltip: {
        trigger: "item"
      },

      //
      visualMap: {
        show: true,
        x: "left",
        y: "center",
        splitList: [
          { start: 500, end: 600 },
          { start: 400, end: 500 },
          { start: 300, end: 400 },
          { start: 200, end: 300 },
          { start: 100, end: 200 },
          { start: 0, end: 100 }
        ],
        color: [
          "#5475f5",
          "#9feaa5",
          "#85daef",
          "#74e2ca",
          "#e6ac53",
          "#9fb5ea"
        ]
      },

      //
      series: [
        {
          name: "",
          type: "map",
          mapType: "china",
          roam: true,
          label: {
            normal: {
              show: true //
            },
            emphasis: {
              show: false
            }
          },
          data: this.mydata //
        }
      ]
    };
    //echarts
    // var myChart = echarts.init(document.getElementById("myChart"));
    let myChart = this.$echarts.init(document.getElementById("myChart"));
    //
    myChart.setOption(optionMap);
  }
};
</script>

