<template>
  <div>
    <el-card :style="{ padding: '0px', width:'100%', position:'fixed',zIndex:'1'}">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/user/userlist' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </el-card>
    <div class="_body">
      <el-card :body-style="{ padding: '20px 10px' }">
        <div slot="header">
          <el-form :inline="true" :model="formInline" class="demo-form-inline">
            <el-form-item label="">
              <el-select v-model="formInline.roid" @change="handleChangeSearch">
                <el-option label="" value="0"></el-option>
                <el-option label="" value="1"></el-option>
                <el-option label="" value="2"></el-option>
                <el-option label="" value="3"></el-option>
                <el-option label="" value="4"></el-option>
                <el-option label="" value="5"></el-option>
                <el-option label="" value="6"></el-option>
              </el-select>
            </el-form-item>
          </el-form>
        </div>
        <div>
          <el-button type="primary" @click="handleAdd"></el-button>
          <el-button type="primary" @click="handleDel"></el-button>
          <el-table
            ref="multipleTable"
            :border="true"
            :data="tableData"
            tooltip-effect="dark"
            style="width: 100%"
            resizable
            stripe
            @selection-change="handleSelectionChange"
          >
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column type="index" width="50"  label="ID"></el-table-column>
            <!-- <el-table-column prop="id" label="ID" width="55"></el-table-column> -->
            <el-table-column label="" >
              <template slot-scope="scope">
                <span v-if="scope.row.roidid == 0"></span>
                <span v-else-if="scope.row.roidid == 1"></span>
                <span v-else-if="scope.row.roidid == 2"></span>
                <span v-else-if="scope.row.roidid == 3"></span>
                <span v-else-if="scope.row.roidid == 4"></span>
                <span v-else-if="scope.row.roidid == 5"></span>
                <span v-else-if="scope.row.roidid == 6"></span>
                <span v-else-if="scope.row.roidid == 7"></span>
              </template>
            </el-table-column>
            <el-table-column prop="roidRangeDis" label="" ></el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-button
                  size="mini"
                  type="primary"
                  @click="handleEdit(scope.$index, scope.row)"
                ></el-button>
                <el-button
                  v-if="scope.row.roidid!=0"
                  size="mini"
                  type="danger"
                  @click="handleDelete(scope.$index, scope.row)"
                ></el-button>
                <el-button
                  v-if="scope.row.roidid==0"
                  size="mini"
                  type="danger"
                  disabled
                  @click="handleDelete(scope.$index, scope.row)"
                ></el-button>
              </template>
            </el-table-column>
          </el-table>
          <el-pagination
            v-if="pageInfo.count"
            background
            layout="prev, pager, next"
            :total="pageInfo.count"
            @current-change="changePage"
          ></el-pagination>
        </div>
      </el-card>
    </div>
    <el-dialog title="" :visible.sync="dialogFormVisible">
      <el-form :model="userinfo" class="dialogForm">
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="userinfo.username" autocomplete="off" style="width:60%"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="userinfo.mobelphone" autocomplete="off" style="width:60%"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="userinfo.email" autocomplete="off" style="width:60%"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="userinfo.pic" style="width:60%"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-radio-group v-model="userinfo.sax">
            <el-radio label="1"></el-radio>
            <el-radio label="0"></el-radio>
          </el-radio-group>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="dialogFormVisible = false"> </el-button>
      </div>
    </el-dialog>
    <el-dialog title="" :visible.sync="dialogEidtVisible">
      <el-form :model="editUserInfo" class="dialogForm">
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="editUserInfo.username" autocomplete="off" style="width:60%"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="editUserInfo.phoneNum" autocomplete="off" style="width:60%"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="editUserInfo.email" autocomplete="off" style="width:60%"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-select v-model="editUserInfo.roidid" style="width:60%">
            <el-option label="" value="0"></el-option>
            <el-option label="" value="1"></el-option>
            <el-option label="" value="2"></el-option>
            <el-option label="" value="3"></el-option>
            <el-option label="" value="4"></el-option>
            <el-option label="" value="5"></el-option>
            <el-option label="" value="6"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-switch v-model="editUserInfo.status" active-text="" inactive-text=""></el-switch>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-radio-group v-model="editUserInfo.sex">
            <el-radio label="1"></el-radio>
            <el-radio label="0"></el-radio>
          </el-radio-group>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogEidtVisible = false"> </el-button>
        <el-button type="primary" @click="dialogEidtVisible = false"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  created() {
    this.$http.get("/user/roidManager.json").then(res => {
      this.tableData = res.data.data;
      this.pageInfo.count = res.data.count;
      // console.log(res.data.count)
    });
  },
  data() {
    return {
      dialogFormVisible: false,
      dialogEidtVisible: false,
      formLabelWidth: "120px",
      userinfo: {
        username: "",
        mobelphone: "",
        email: "",
        pic: "",
        sax: "1"
      },
      formInline: {
        id: "",
        username: "",
        email: "",
        roid: "0"
      },
      tableData: [],
      //  
      multipleSelection: [],
      editUserInfo: {
        id: "",
        username: "",
        phoneNum: "",
        email: "",
        roidid: "",
        status: "",
        sax: ""
      },
      pageInfo: {
        count: 0,
        limit: "",
        index: ""
      }
    };
  },
  methods: {
    handleChangeSearch(value) {
      this.$message(value);
    },
    onSubmit() {
      console.log("submit!");
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    handleAdd() {
      for (const i in this.userinfo) {
        if (this.userinfo.hasOwnProperty(i)) {
          this.userinfo[i] = "";
        }
      }
      this.dialogFormVisible = true;
    },
    handleDel() {},
    handleEdit(index, row) {
      this.dialogEidtVisible = true;
      this.editUserInfo = Object.assign({}, row);
    },
    handleDelete() {},
    changePage(index) {
      console.log(index);
      this.pageInfo.index = index;
    }
  }
};
</script>

<style scoped>
._body {
  padding: 10px;
  padding-top: 70px;
}
._body ._label {
  color: #ccc;
  margin: 0 10px;
}
._body .tips {
  color: #ccc;
}
.el-form-item {
  margin-bottom: 0px;
}
.el-col-6 {
  text-align: center;
}
.block {
  display: -webkit-flex; /* Safari */
  display: flex;
  justify-content: center;
  margin: 5px 0;
}
.dialogForm .el-form-item {
  margin-bottom: 22px;
}
.name-wrapper {
  text-align: center;
}
</style>
